<template>
  <div style="background-color: #F8F8F8;height: calc(100vh - 47px)">
    <div class="headerBox">
      <div class="userInfo">
        <h1 class="userName">{{name}}</h1>
        <p class="info">&nbsp;{{address}}</p>
        <p class="info">
          <i class="iconfont">&#xe620;</i>&nbsp;{{dept}}
        </p>
      </div>
      <img class="userHeader" src="../../assets/head.png">
    </div>
    <div class="userOther">
      <mt-cell title="问题反馈" to="/problemFeedback" is-link></mt-cell>
      <mt-cell title="我的信息" to="/myInformation" is-link></mt-cell>
      <mt-cell title="推荐养户" to="/register" is-link></mt-cell>
      <mt-cell title="解绑" @click.native.prevent="loginOut()" is-link></mt-cell>
    </div>
    <bottom-nav></bottom-nav>
  </div>
</template>

<script>
  import bottomNav from "@/components/bottomNav";
  import loginWeixin from '@/components/LoginWeixin'
  import {getFeedPersonInfo} from '@/api/userCenter'
  import {unbind} from "@/api/bindInfo"
  import {Toast} from 'mint-ui'
  
  export default {
    name: "personInfo",
    components: {
      bottomNav,
      loginWeixin
    },
    data() {
      return {
        name: '',
        address: '',
        dept: ""
      }
    },
    created() {
      this._getUserInfo();
    },
    methods: {
      loginOut() {
        this.$messagebox.confirm('确定执行此操作?').then(action => {
          let _this = this;
          unbind().then((res) => {
            _this.$toast("解绑成功！");
            _this.$store.commit('SET_USERINFO', '');
            setTimeout(function () {
              _this.$router.push("/bindInfo");
            }, 2000)
          }).catch((err) => {
            Toast("解绑失败！" + err);
          });
        });
      },
      _getUserInfo() {
        getFeedPersonInfo().then((res) => {
          let data = res.data;
          this.name = data.fname;
          this.address = data.cfpigpen;
          this.dept = data.servicedep
        }).catch((err) => {
          // this.$toast(JSON.stringify(err));
        })
      }
    }
  };
</script>

<style scoped lang="scss">
  .headerBox {
    display: flex;
    flex-flow: nowrap row;
    padding: 0.10rem 0.14rem 0.25rem;
    background-color: #fff;
    border-bottom: 1px solid #ebebeb;
    
    & .userName {
      margin: 0.1rem 0;
      font-size: 0.18rem;
      font-weight: normal;
    }
    
    & .info {
      margin: 0;
      font-size: 0.12rem;
      color: #8a8e96;
      font-weight: 500;
    }
    
    & .userInfo {
      flex: 1;
    }
    
    & .userHeader {
      width: 0.75rem;
      height: 0.75rem;
      border: 1px dashed #797676;
    }
  }
  
  .cell-list {
    margin: 0.12rem 0;
    
    & .cell:not(:first-child) {
      border-top: none;
    }
  }
  
  .userOther {
    margin-top: 0.2rem;
  }
  
  .cell {
    padding: 0.12rem 0.12rem;
    background-color: #fff;
    border-bottom: 1px solid #ebebeb;
    border-top: 1px solid #ebebeb;
    position: relative;
    
    &:active {
      background-color: #e1e1e1;
    }
  }
  
  .mint-cell {
    border-bottom: 1px solid #ebebeb;
  }
  
  .unbind-block {
    position: fixed;
    bottom: .8rem;
    width: 100%;
  }
</style>



