<template>
  <div></div>
</template>
<script>
  import {getUserInfo,setUserInfo} from '@/utils/auth'
  import {login,getUserInfoData} from '@/api/bindInfo'
  export default {
    name: 'LoginWeixin',
    mounted(){
      this._checkWeixin()
    },
    methods:{
      _checkWeixin(){
        if(getUserInfo()&&getUserInfo()!==""){
        }else {
          login().then((res)=>{
            if(res.status === 991){
              this.$router.push('/bindInfo');
            }else if(res.status ===200){
              getUserInfoData().then((res)=>{
                let data= res.data;
                let userInfo={
                  id:data.id,
                  name:data.name,
                  size:data.status
                }
                this.$store.commit("SET_USERINFO",userInfo)
              }).catch((err)=>{

              })
            }

          }).catch((err)=>{
            this.$router.push('/bindInfo')
          })
        }
      }
    }
  }
</script>

<style scoped>

</style>



